.dropdown {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: white;
  text-align: left;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-color: #0f2537;
  border: 1px solid #0f2537;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.375rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.attributes-positions-dropdowns {
  display: flex;
}

.attribute-dropdown-div {
  display: block;
  width: 25%;
  margin-right: 1rem;
}

.attributes-dropdown {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.position-dropdown-div {
  display: block;
  width: 13%;
}

.position-dropdown {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.position-dropdown-and-clear {
  position: relative;
  display: inline-block;
}

.sample-dropdown {
  padding-right: 30px
}

.sample-dropdown-and-clear {
  position: relative;
  display: inline-block;
}

.dataset-dropdown {
  margin-bottom: 1rem;
}

.radar-dropdowns {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem
}

.attributes-label {
  color: #0f2537;
  font-size: 15px;
}

.radar-buttons {
  margin-top: 0.5rem;
  display: flex;
  gap: 0.5rem;
}

.clear-position-filter-button {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  z-index: 1; /* Ensure it's above the select */
}

.clear-position-filter-button:hover {
  background-color: rgba(0, 0, 0, 0.1);
  color: #333;
}

.clear-sample-filter-button {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  z-index: 1; /* Ensure it's above the select */
}

.clear-sample-filter-button:hover {
  background-color: rgba(0, 0, 0, 0.1);
  color: #333;
}

/* For the close icon */
.close-icon {
  position: relative;
  display: inline-block;
  width: 12px;
  height: 12px;
}

.close-icon::before,
.close-icon::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(45deg);
}

.close-icon::after {
  transform: translateY(-50%) rotate(-45deg);
}

.selected-metrics-dropdown-div {
  position: relative;
  display: block;
  width: fit-content;
  
}

.edit-button-inside {
  position: absolute;
  right: 25px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
}