* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

h1 {
  color: #0f2537;
  font-size: 70px;
  font-family: 'Sahar-Black', fantasy;
  letter-spacing: 0;
  text-transform: uppercase;
  font-style: bold;
}

h3 {
  color: #0f2537;
  font-size: 30px;
  font-family: 'Sahar-Black', fantasy;
}

p {
  color: #0f2537;
  font-size: 20px;
}

label {
  color: #e8e4d6;
}
