.file-upload-dropzone {
  width: 150px;
  line-height: 60px;
  border-width: 1px;
  border-style: dashed;
  border-radius: 5px;
  text-align: center;
  margin: 5px;
  margin-left: -1px;
  border-color: #ccc;
  background-color: transparent; 
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

/* Styles applied when isDragOver is true */
.file-upload-dropzone.drag-over {
  border-color: #007bff; /* Highlight color */
  background-color: #f0f8ff; /* Highlight background */
}

/* Styles for the hidden input */
.file-upload-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.file-loading-div {
  display: 'flex';
  justify-content: 'center';
  align-items: 'center';
  height: '100%';
}

/* Basic CSS for a loading spinner */
.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border-left-color: #09f;
  animation: spin 1s ease infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.upload-success {
  color: #28a745;
  font-weight: bold;
}
