html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden; /* Prevents page-level scrolling */
}

.dashboard-container {
  background-color: #0f2537;
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  gap: 15px; /* Space between flex items */
  padding: 15px; /* Overall container padding */
}

.left-container {
  flex: 0 0 120px; /* Fixed width for sidebar */
  padding: 10px;
  height: 100%;
  box-sizing: border-box;
}

.right-container {
  flex: 1; /* Takes remaining space */
  background-color: #e8e4d6;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 20px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;
}

.home-button {
  margin-top: 50px;
  text-align: left;
  background-color: transparent;
  border: 1px solid transparent;
}

.home-logo {
  border-radius: 50%;
  width: 80%;
}

.home-button:hover {
  background-color: transparent;
  border: 1px solid transparent;
}

.home-button:active {
  background-color: transparent !important;
  border: 1px solid transparent !important;
}

.btn {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.375rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.container-button {
  border-radius: 5px;
  color: #f9f5ec;
  background-color: #e2777d;
}

.container-button:hover {
  background-color: #cd5b45;
}

.download-div {
  text-align: left;
  margin-top: 30px;
  margin-left: 10px;
}

.download-button {
  margin-top: 5px;
}

.data-selection-div {
  text-align: left;
  margin-top: 50px;
  margin-left: 10px;
}

.stats-attributes-button-div {
  margin-top: 5px;
  display: flex;
  gap: 0.5rem;
}

.upload-div {
  text-align: left;
  font-size: 14px;
  margin-top: 50px;
  margin-left: 10px;
}

.sample-data-div {
  text-align: left;
  font-size: 14px;
  margin-left: 10px;
}

.feedback-report-div {
  text-align: left;
  margin-top: 50px;
  margin-left: 10px;
}

.feedback-report-button-div {
  margin-top: 5px;
  display: flex;
  gap: 0.5rem;
}

/* Labels */
label {
color: #e8e4d6;
}

.stats-label {
color: #0f2537;
font-size: 17.5px;
}
