/* Drawer Form Styling to match your app */
.drawer-form {
  background: linear-gradient(135deg, #0f2537 0%, #1a3a52 100%);
  color: #e8e4d6;
  padding: 20px;
  height: 100vh;
  overflow-y: auto;
}

.drawer-form h2 {
  color: #e8e4d6;
  font-weight: 600;
  font-size: 1.3em;
  margin: 0 0 20px 0;
  text-align: center;
  padding-bottom: 15px;
  border-bottom: 2px solid rgba(232, 228, 214, 0.2);
}

.form-group {
  margin-bottom: 25px;
}

.form-label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #e8e4d6;
  font-size: 0.95em;
}

.form-input,
.form-textarea,
.form-select {
  width: 100%;
  padding: 12px 15px;
  border: 2px solid rgba(232, 228, 214, 0.3);
  border-radius: 12px;
  background-color: rgba(232, 228, 214, 0.95);
  color: #0f2537;
  font-size: 14px;
  font-family: inherit;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
  outline: none;
  border-color: #4a9eff;
  background-color: #ffffff;
  box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.1);
  transform: translateY(-1px);
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: rgba(15, 37, 55, 0.6);
  font-style: italic;
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
  font-family: inherit;
}

.form-select {
  cursor: pointer;
}

.form-select option {
  background-color: #e8e4d6;
  color: #0f2537;
  padding: 8px;
}

.submit-button {
  width: 100%;
  padding: 15px 20px;
  background-color: #e2777d;
  color: #f9f5ec;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.submit-button:hover {
  background-color: #cd5b45;
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(226, 119, 125, 0.3);
}

.submit-button:active {
  transform: translateY(0);
  box-shadow: 0 4px 10px rgba(74, 158, 255, 0.2);
}

.form-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(232, 228, 214, 0.3), transparent);
  margin: 25px 0;
  border: none;
}

/* Optional: Add some spacing between form groups */
.form-group:last-of-type {
  margin-bottom: 30px;
}

/* Scrollbar styling for the drawer */
.drawer-form::-webkit-scrollbar {
  width: 8px;
}

.drawer-form::-webkit-scrollbar-track {
  background: rgba(232, 228, 214, 0.1);
  border-radius: 4px;
}

.drawer-form::-webkit-scrollbar-thumb {
  background: rgba(232, 228, 214, 0.4);
  border-radius: 4px;
}

.drawer-form::-webkit-scrollbar-thumb:hover {
  background: rgba(232, 228, 214, 0.6);
}